
import { defineComponent, ref, watch } from 'vue';
import { useRoute } from 'vue-router';

export default defineComponent({
  setup() {
    const route = useRoute();
    const path = ref(route.path);
    watch(
      () => route.path,
      async (newPath) => {
        path.value = newPath;
      },
    );
    return {
      path,
    };
  },
});
