
import { defineComponent, ref } from 'vue';

const BASE_SIZE = 24;

export default defineComponent({
  props: {
    color: {
      type: String,
      default: 'rgb(var(--c-live-bg))',
    },
    size: {
      type: Number,
      default: 24,
    },
  },
  setup(props) {
    const style = ref({
      fill: props.color,
      transform: `scale(${props.size / BASE_SIZE})`,
    });
    return {
      style,
    };
  },
});
