
import { defineComponent, computed } from 'vue';

const BASE_SIZE = 24;

export default defineComponent({
  props: {
    color: {
      type: String,
      default: 'rgb(var(--c-live-bg))',
    },
    size: {
      type: Number,
      default: 24,
    },
    outlined: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    // const style = ref({
    //   fill: props.color,
    //   transform: `scale(${props.size / BASE_SIZE})`,
    // });
    const style = computed(() => {
      if (props.outlined) {
        return {
          stroke: props.color,
          fill: 'transparent',
          transform: `scale(${props.size / BASE_SIZE})`,
        };
      }
      return {
        fill: props.color,
        transform: `scale(${props.size / BASE_SIZE})`,
      };
    });
    return {
      style,
    };
  },
});
