
import { defineComponent, ref } from 'vue';

export default defineComponent({
  props: {
    size: {
      type: Number,
      default: 24,
    },
  },
  setup(props) {
    const style = ref({
      height: `${props.size}px`,
      width: `${props.size}px`,
      borderRadius: `${props.size / 2}px`,
    });
    return {
      style,
    };
  },
});
